@import "..//../variables.scss";

.form {
  box-shadow: 0px 1px 2px 0px rgba(22, 41, 80, 1);
  //outline: 1px solid red;
  padding: 32px 40px;
  border-radius: 32px;
  background: #fdfdfd;

  display: flex;
  flex-direction: column;

  width: 496px;
  margin-left: auto;
  margin-right: auto;
}

.title {
  margin-bottom: 40px;

  font-size: 36px;
  font-weight: 600;
  line-height: 1.33;
  color: $text-color;
}

.input__wrapper {
  position: relative;
  margin-bottom: 24px;
}

.label {
  display: block;
  margin-bottom: 6px;

  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  color: #494949;
}

.input {
  display: block;
  width: 100%;
  padding: 10px 14px;
  border-radius: 8px;
  border: 1px solid #bababa;
  background: #fefefe;
  box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);

  font-size: 16px;
  font-weight: 400;
  line-height: 1.71;
  color: #8d8d8d;
}

.error {
  position: absolute;
  bottom: 5px;
  right: 14px;
  color: rgb(241, 87, 87);
}

.error_password {
  color: #618499;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.77;

  position: absolute;
  bottom: -30px;
  right: 14px;

  cursor: pointer;
}

.button {
  padding: 16px 0;
  margin-bottom: 16px;
  text-align: center;
  background-color: $main-color;
  border: none;
  border-radius: 999px;
  box-shadow: 0px 1px 2px 0px rgba(198, 228, 246, 0.05);

  color: $btn-text;

  font-size: 21px;
  font-weight: 800;
  line-height: 1.14;

  cursor: pointer;
}

.link {
  color: $second-text-color;

  font-size: 18px;
  font-weight: 400;
  line-height: 1.77;
}

.checkbox__wrapper {
  margin-bottom: 32px;
}

.checkbox__field {
  position: absolute;
  top: 50%;
  left: 20px;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.checkbox__icon_true {
  width: 16px;
  height: 16px;
  border-radius: 4px;
  background-color: $main-color;
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
}

.checkbox__icon_false {
  width: 16px;
  height: 16px;
  border-radius: 4px;
  background-color: transparent;
  border: 1px solid #bababa;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
}

.checkbox {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  cursor: pointer;
}
.checkbox:last-child {
  margin-bottom: 0;
}

.checkbox__text {
  color: $second-text-color;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.77;
}

.redirect_link {
  text-align: center;
}

.iconPassword {
  position: absolute;
  top: 55%;
  right: 14px;
  fill: $text-color;
  cursor: pointer;
}
